/* ðŸ“Œ Galeri Genel TasarÄ±m */
.gallery-container {
    text-align: center;
    padding: 50px 20px;
    max-width: 1200px;
    margin: auto;
  }
  
  .gallery-title {
    font-size: 2.5rem;
    font-weight: bold;
    color: #003366;
    margin-bottom: 20px;
  }
  
  .gallery-description {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 40px;
  }
  
  /* ðŸ“Œ Galeri Grid Sistemi */
  .gallery-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    justify-items: center;
  }
  
  /* ðŸ“Œ Galeri KartlarÄ± */
  .gallery-item {
    width: 100%;
    height: 200px;
    background-color: #ddd;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    color: #003366;
    border-radius: 8px;
    transition: transform 0.3s ease-in-out;
  }
  
  /* ðŸ“Œ Hover Efekti */
  .gallery-item:hover {
    transform: scale(1.05);
    background-color: #00bcd4;
    color: white;
  }
  